/**
 * @fileoverview This file defines the main Employee Dashboard page.
 * It serves as the landing page for employees after they log in,
 * showing a profile summary, company announcements, and a calendar.
 */
import {
  Card,
  CardContent,
  CardHeader,
  CardTitle,
  CardDescription,
} from "@/components/ui/card";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Separator } from "@/components/ui/separator";
import { Calendar } from "@/components/ui/calendar";
import { Megaphone } from "lucide-react";

/**
 * Renders the main dashboard page for an employee.
 * It displays employee information, announcements, and a calendar.
 * @returns {JSX.Element} The employee dashboard page component.
 */
export default function EmployeeDashboardPage() {
  // --- DATABASE INTEGRATION POINT ---
  // In a real application, this data would be fetched from a database based on the logged-in user.
  const employee = {
      name: 'Jane Doe',
      position: 'Senior Software Engineer',
      employeeId: 'EMP-00123',
      department: 'Technology',
      email: 'jane.doe@example.com'
  };

  // --- DATABASE INTEGRATION POINT ---
  // In a real application, this would be fetched from a database.
  const announcements = [
      { id: 1, title: 'Annual Company Picnic', date: 'August 15, 2024', content: 'Join us for our annual company picnic at Central Park on August 30th! Food, games, and fun for the whole family. Please RSVP by August 25th.' },
      { id: 2, title: 'Q3 Performance Reviews', date: 'August 10, 2024', content: 'Q3 performance reviews are starting next week. Please schedule a meeting with your manager to discuss your progress and goals.' },
      { id: 3, title: 'New System Update', date: 'August 5, 2024', content: 'The HR portal will be undergoing scheduled maintenance this Friday at 10 PM. The system will be unavailable for approximately 2 hours.' },
  ];

  return (
    <div className="grid gap-6 md:grid-cols-3">
      <div className="md:col-span-2 space-y-6">
        {/* Employee Profile Card: Displays key information about the logged-in employee. */}
        <Card>
            {employee ? (
                <>
                    <CardHeader>
                        <div className="flex items-center gap-4">
                        <Avatar className="h-16 w-16">
                            <AvatarImage
                            src={`https://i.pravatar.cc/150?u=${employee.email}`}
                            alt={employee.name}
                            />
                            <AvatarFallback>
                            {employee.name
                                .split(" ")
                                .map((n) => n[0])
                                .join("")}
                            </AvatarFallback>
                        </Avatar>
                        <div>
                            <CardTitle className="font-headline text-2xl">{employee.name}</CardTitle>
                            <CardDescription>{employee.position}</CardDescription>
                        </div>
                        </div>
                    </CardHeader>
                    <CardContent>
                        <div className="grid grid-cols-2 gap-4 text-sm">
                        <div>
                            <p className="font-medium text-muted-foreground">
                            Employee ID
                            </p>
                            <p>{employee.employeeId}</p>
                        </div>
                        <div>
                            <p className="font-medium text-muted-foreground">Department</p>
                            <p>{employee.department}</p>
                        </div>
                        <div>
                            <p className="font-medium text-muted-foreground">Email</p>
                            <p>{employee.email}</p>
                        </div>
                        </div>
                    </CardContent>
                </>
            ) : (
                <CardContent className="p-6">
                    <p className="text-muted-foreground">Employee data not available.</p>
                </CardContent>
            )}
        </Card>

        {/* Announcements Card: Shows the latest company-wide announcements. */}
        <Card>
          <CardHeader>
            <CardTitle className="flex items-center gap-2">
              <Megaphone />
              Announcement Board
            </CardTitle>
          </CardHeader>
          <CardContent>
            {announcements.length > 0 ? (
                <ul className="space-y-4">
                {announcements.map((ann, index) => (
                    <li key={ann.id}>
                    <p className="font-semibold">{ann.title}</p>
                    <p className="text-sm text-muted-foreground">{ann.date}</p>
                    <p className="text-sm mt-1">{ann.content}</p>
                    {index < announcements.length - 1 && (
                        <Separator className="mt-4" />
                    )}
                    </li>
                ))}
                </ul>
            ) : (
                <p className="text-muted-foreground">No announcements at this time.</p>
            )}
          </CardContent>
        </Card>
      </div>
      {/* Calendar Card: Displays a simple calendar, could be used for upcoming events or schedules. */}
      <div className="md:col-span-1">
        <Card>
          <CardHeader>
            <CardTitle>Calendar</CardTitle>
          </CardHeader>
          <CardContent className="flex justify-center">
            <Calendar
              mode="single"
              selected={new Date()}
              className="rounded-md"
            />
          </CardContent>
        </Card>
      </div>
    </div>
  );
}
